.projectContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 280px);
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    padding: 2.5em;
    margin: 2.5em 0;
}
.projectPara {
    margin-top: 5em;
    margin-left: 10em;    
    text-align: left;
}
.projectPara h1 {
    text-align: left;
    font-size: 50px;
}
.projectPara p {
    font-size: 20px;
    font-weight: 200;
}
.projectGrid {
    display: grid;
    grid-column: 2/2;
    grid-row: 2/2;
    grid-template-columns: repeat(2, 300px);
    grid-template-rows: repeat(2, 230px);
    justify-content: center;
    align-items: center;
    margin-bottom: 15em;
}
.project0,.project2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 20vw;
    height: 30vh;
    padding: 0.7em;
    background-image: linear-gradient(to right top, #002d5580, #00567780, #007f8280, #50a67c80, #aec67980);
    margin-bottom: 5em;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-left-radius: 30px;
    box-shadow: 5px 10px 10px 0px rgba(0,0,10,0.08);

}
.project0Header, .project1Header, .project2Header, .project3Header {
    margin-right: 4em;
}
.project3Header {
    margin-bottom: 2.5em;
}
.project0Link, .project1Link, .project2Link, .project3Link {
    margin-left: 11em;
    margin-top: 1em;
}
.project0Link a, .project1Link a, .project2Link a, .project3Link a {
    transition: 0.3s;
    text-decoration: none;
    color: black;
}
.project0Link a:hover, .project1Link a:hover, .project2Link a:hover, .project3Link a:hover {
    color: #ffffffdd;
}
.project1, .project3 {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(to right top, #002d5580, #00567780, #007f8280, #50a67c80, #aec67980);
    box-shadow: -5px 10px 10px 0px rgba(0,0,10,0.08);
    justify-content: center;
    align-items: center;
    width: 20vw;
    height: 30vh;
    padding: 0.5em;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    border-bottom-left-radius: 30px;
}
